import { useContext, useEffect, useState } from "react";
import { useForm } from "react-hook-form";

//internal import
import { SidebarContext } from "context/SidebarContext";
import SettingServices from "services/SettingServices";
import { notifyError, notifySuccess } from "utils/toast";
import { createVendor, updateVendor } from "services/Api";
import { useHistory } from "react-router-dom";

const useSettingSubmit = (selectedChequeFile, selectedGstFile, id) => {
  const { setIsUpdate } = useContext(SidebarContext);
  const [isSave, setIsSave] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const history = useHistory();
  const {
    register,
    handleSubmit,
    setValue,
    reset,
    formState: { errors },
  } = useForm();
  const resetBtn = () => {
    history.replace('/vendors');
  reset()
  }
  const onSubmit = async (data) => {
    console.log(data, "datadata");

    try {
      setIsSubmitting(true);
      const payload = {
        property_id: data?.property_id,
        service_category: data?.category,
        service_description: data?.type_of_service,
        company_name: data?.name,
        mobile: data?.phone,
        email: data?.email,
        gst_file_path: selectedGstFile,
        cheque_file_path: selectedChequeFile,
        address: data?.address,
        city: data?.city,
        zipcode: data?.postalCode,
        state_id: data?.state_id,
        bank_name: data?.bank_name,
        bank_account_number: data?.account_number,
        bank_ifsc_code: data?.ifsc_code,
        bank_branch_name: data?.branch_name,
        person_to_contact_name: data?.person_name,
        person_to_contact_mobile: data?.person_phone,

        gst_number: data?.gst_number,
      };
      if (id) {
        payload.id = id;
      }

      let res = {};
      if (id) res = await updateVendor(payload);
      else res = await createVendor(payload);
      if (res.data.status == true) {
        notifySuccess(res.data.message);
        history.replace("/vendors");
      }
    } catch (err) {
      notifyError(err ? err?.response?.data?.message : err.message);
      setIsSubmitting(false);
    }
  };

  return {
    errors,
    register,
    handleSubmit,
    resetBtn,
    onSubmit,
    isSave,
    setValue,
    isSubmitting,
    setValue,
  };
};

export default useSettingSubmit;
