import { Alert, Button, Select } from "@windmill/react-ui";
import { useTranslation } from "react-i18next";
import { useDropzone } from "react-dropzone";

import { FiUploadCloud, FiXCircle } from "react-icons/fi";
import Multiselect from "multiselect-react-dropdown";
import { MultiSelect } from "react-multi-select-component";
import Error from "components/form/Error";
import spinnerLoadingImage from "assets/img/spinner.gif";
import InputAreaTwo from "components/form/InputAreaTwo";
import SelectTimeZone from "components/form/SelectTimeZone";
import PageTitle from "components/Typography/pageTitleButton";
import useSettingSubmit from "hooks/useVendorSubmit";
import SelectCurrency from "components/form/SelectCurrency";
import LabelArea from "components/form/LabelArea";
import SelectReceiptSize from "components/form/SelectPrintSize";
import Uploader from "components/image-uploader/Uploader";
import useProductSubmit from "hooks/useProductSubmit";
import image1 from "../../assets/img/day1.png";
import image2 from "../../assets/img/day2.png";
import {
  getAllCommonProperyList,
  getAllCommonStateList,
  uploadDocImage,
} from "services/Api";
import { useEffect, useState } from "react";
import { notifyError, notifySuccess } from "utils/toast";
import { useRef } from "react";

const Setting = () => {
  const [selectedChequeFile, setChequeSelectedFile] = useState(null);
  const [selectedGstFile, setGstSelectedFile] = useState(null);
  const {
    errors,
    register,
    handleSubmit,
    onSubmit,
    resetBtn,
    isSave,
    isSubmitting,
  } = useSettingSubmit(selectedChequeFile, selectedGstFile);
  const { t } = useTranslation();
  const selectedCategory = [
    { name: "Option 1️⃣", id: 1 },
    { name: "Option 2️⃣", id: 2 },
  ];
  const { imageUrl, setImageUrl } = useProductSubmit();
  const [propertyList, setPropertyList] = useState([]);
  const [stateList, setStateList] = useState([]);

  const fileInputRef = useRef(null);
  const fileInputRef1 = useRef(null);

  const getPropertyList = async () => {
    const PropertyList = await getAllCommonProperyList();
    setPropertyList(PropertyList?.data?.data?.propertyList);
  };
  const getStateList = async () => {
    const res = await getAllCommonStateList();
    setStateList(res?.data?.data?.states);
  };

  const handleChequeFileInputChange = async (event) => {
    const file = event.target.files[0];

    const formData = new FormData();
    formData.append("file", file);
    try {
      const response = await uploadDocImage(formData);
      if (response && response.status === 200) {
        console.log(response, "response");
        setChequeSelectedFile(response.data?.data.filePath);
        notifySuccess(response.data.message);
      }
    } catch (e) {
      notifyError(
        e.response?.data ? e.response?.data?.message : "something went wrong"
      );
    }
  };
  const handleGstCertificateFileInputChange = async (event) => {
    const file = event.target.files[0];

    const formData = new FormData();
    formData.append("file", file);
    try {
      const response = await uploadDocImage(formData);
      if (response && response.status === 200) {
        console.log(response, "response");
        setGstSelectedFile(response.data?.data.filePath);
        notifySuccess(response.data.message);
      }
    } catch (e) {
      notifyError(
        e.response?.data ? e.response?.data?.message : "something went wrong"
      );
    }
  };

  const handleChequeUploadButtonClick = () => {
    fileInputRef.current.click();
  };
  const handleGstCertificateUploadButtonClick = () => {
    fileInputRef1.current.click();
  };
  useEffect(() => {
    getPropertyList();
    getStateList();
  }, []);

  return (
    <>
      <PageTitle resetBtn={resetBtn}>{t("Add Vendors")}</PageTitle>

      <div className="container p-6 mx-auto bg-white  dark:bg-gray-800 dark:text-gray-200 rounded-lg">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="grid grid-cols-12 font-sans">
            <div className="col-span-12 md:col-span-12 lg:col-span-12 mr-3 ">
              <div className="flex-grow scrollbar-hide w-full max-h-full pb-0">
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select property")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`property_id`, {
                          required: "Select property is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Property")}
                        </option>
                        {propertyList?.map((item) => (
                          <option value={item.id} key={item?.id}>
                            {item?.name}
                          </option>
                        ))}
                      </Select>
                      <Error errorName={errors.property_id} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Name")}
                        name="name"
                        type="text"
                        required
                        placeholder={t("Enter Name")}
                      />
                      <Error errorName={errors.name} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("GST Number")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("GST Number")}
                        name="gst_number"
                        type="text"
                        placeholder={t("Enter GST Number")}
                      />
                      <Error errorName={errors.gst_number} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-3">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Select Category")}
                    </label>

                    <div className="sm:col-span-3">
                      <Select
                        {...register(`category`, {
                          required: "Select Category is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" defaultValue hidden>
                          {t("Select Category")}
                        </option>
                        <option value="procurement">Procurement</option>
                        <option value="b_to_b_customer">BtoB customer</option>
                        <option value="b_to_c_customer">BtoC customer</option>
                      </Select>
                      <Error errorName={errors.category} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Type of service/product")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Type of service/product")}
                        name="type_of_service"
                        type="text"
                        required
                        placeholder={t("Enter Type of service/product")}
                      />
                      <Error errorName={errors.type_of_service} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Address")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Address")}
                        name="address"
                        type="text"
                        placeholder={t("Enter Address")}
                      />
                      <Error errorName={errors.address} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-1">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("City")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("City")}
                        name="city"
                        type="text"
                        placeholder={t("Enter City")}
                      />
                      <Error errorName={errors.city} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-1">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("State")}
                    </label>
                    <div className="sm:col-span-3">
                      <Select
                        {...register(`state_id`, {
                          // required: "Select property is required",
                        })}
                        className="border h-12 text-sm focus:outline-none block w-full bg-gray-100 border-transparent focus:bg-white"
                      >
                        <option value="" hidden>
                          {t("Select state")}
                        </option>
                        {stateList?.map((item) => (
                          <option value={item.id} key={item?.id}>
                            {item?.name}
                          </option>
                        ))}
                      </Select>
                      <Error errorName={errors.state_id} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Postal Code")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Postal Code")}
                        name="postalCode"
                        type="text"
                        placeholder={t("Enter Postal code")}
                      />
                      <Error errorName={errors.postalCode} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Phone")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("phone")}
                        name="phone"
                        required
                        type="text"
                        maxLength={10}
                        placeholder={t("Enter Phone")}
                      />
                      <Error errorName={errors.phone} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Email")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("email")}
                        name="email"
                        required
                        type="text"
                        placeholder={t("Enter Email")}
                      />
                      <Error errorName={errors.email} pos />
                    </div>
                  </div>
                </div>
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Person Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Person Name")}
                        name="person_name"
                        type="text"
                        placeholder={t("Enter Person Name")}
                      />
                      <Error errorName={errors.person_name} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Person Phone")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Person Phone")}
                        name="person_phone"
                        type="text"
                        placeholder={t("Enter Person Phone")}
                      />
                      <Error errorName={errors.person_phone} pos />
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Bank Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Bank Name")}
                        name="bank_name"
                        type="text"
                        placeholder={t("Enter Bank name")}
                      />
                      <Error errorName={errors.bank_name} pos />
                    </div>
                  </div>
                </div>

                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Branch Name")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Branch Name")}
                        name="branch_name"
                        type="text"
                        placeholder={t("Enter Branch name")}
                      />
                      <Error errorName={errors.branch_name} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Account Number")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("Account Number")}
                        name="account_number"
                        type="text"
                        placeholder={t("Enter Account Number")}
                      />
                      <Error errorName={errors.account_number} pos />
                    </div>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("IFSC Code")}
                    </label>
                    <div className="sm:col-span-3">
                      <InputAreaTwo
                        register={register}
                        label={t("IFSC code")}
                        name="ifsc_code"
                        type="text"
                        placeholder={t("Enter IFSC code")}
                      />
                      <Error errorName={errors.ifsc_code} pos />
                    </div>
                  </div>
                </div>
                <div className="grid md:grid-cols-6 items-center sm:grid-cols-12 gap-3 md:gap-5 xl:gap-4 lg:gap-4 mb-6">
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("Cancelled Cheque Image")}
                    </label>
                    <div className="">
                      <input
                        ref={fileInputRef}
                        type="file"
                        style={{ display: "none" }}
                        onChange={handleChequeFileInputChange}
                      />
                    </div>
                    <div
                      onClick={handleChequeUploadButtonClick}
                      className="border-2 border-gray-300 dark:border-gray-600 border-dashed rounded-md cursor-pointer px-6 pt-5 pb-6"
                    >
                      {selectedChequeFile ? (
                        <img src={selectedChequeFile} alt="doc" />
                      ) : (
                        <>
                          <span className="mx-auto flex justify-center">
                            <FiUploadCloud className="text-3xl text-green-500" />
                          </span>
                          <p className="text-sm mt-2">{t("DragYourImage")}</p>
                          <em className="text-xs text-gray-400">
                            {t("imageFormat")}
                          </em>
                        </>
                      )}
                    </div>
                    <Alert className="sm12 mt-2" type="info">
                      {" "}
                      Image should be 670 × 540px and Image Size should be less
                      than 5mb
                    </Alert>
                  </div>
                  <div className="sm:col-span-2">
                    <label className="block text-sm font-semibold text-gray-600 dark:text-gray-400 mb-1 sm:col-span-2">
                      {t("GST Certificate")}
                    </label>
                    <div className="">
                      <input
                        ref={fileInputRef1}
                        type="file"
                        style={{ display: "none" }}
                        onChange={handleGstCertificateFileInputChange}
                      />
                    </div>
                    <div
                      onClick={handleGstCertificateUploadButtonClick}
                      className="border-2 border-gray-300 dark:border-gray-600 border-dashed rounded-md cursor-pointer px-6 pt-5 pb-6"
                    >
                      {selectedGstFile ? (
                        <img src={selectedGstFile} alt="doc" />
                      ) : (
                        <>
                          <span className="mx-auto flex justify-center">
                            <FiUploadCloud className="text-3xl text-green-500" />
                          </span>
                          <p className="text-sm mt-2">{t("DragYourImage")}</p>
                          <em className="text-xs text-gray-400">
                            {t("imageFormat")}
                          </em>
                        </>
                      )}
                    </div>
                    <Alert className="sm12 mt-2" type="info">
                      {" "}
                      Image should be 670 × 540px and Image Size should be less
                      than 5mb
                    </Alert>
                  </div>
                </div>

                <div className="flex flex-row-reverse pb-6">
                  <Button
                    type="button"
                    className="h-12 px-8"
                    onClick={() => resetBtn()}
                    style={{
                      position: "relative",
                      right: "22em",
                      backgroundColor: "gainsboro",
                      color: "red",
                    }}
                  >
                    Cancel
                  </Button>
                  {isSubmitting ? (
                    <Button disabled={true} type="button" className="h-12">
                      <img
                        src={spinnerLoadingImage}
                        alt="Loading"
                        width={20}
                        height={10}
                      />{" "}
                      <span className="font-serif ml-2 font-light">
                        Processing
                      </span>
                    </Button>
                  ) : (
                    <Button type="submit" className="h-12 px-8">
                      {" "}
                      {isSave ? "Save" : "Update"}
                    </Button>
                  )}
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </>
  );
};
export default Setting;
